header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}


.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* =========== CV ========= */
.cv {
  margin-top: 2.5rem;
  display: -ms-flexbox;
  display: flex;
  gap: 1.2rem;
  -ms-flex-pack: center;
      justify-content: center;
}

/* =========== Header SociaLMedia ========= */
.header__socialmedia {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem; /*3rem*/
}

.header__socialmedia::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/* ========== ME =========*/
.me {
  background: -webkit-gradient(linear, left top, left bottom, from(var(--color-primary)), to(transparent));
  background: -webkit-linear-gradient(var(--color-primary), transparent);
  background: -o-linear-gradient(var(--color-primary), transparent);
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position:inherit;
  left: calc(50% - 11rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}


/* ===========Scroll Down ==========*/
.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem; /* 5rem */
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* ============ MEDIA QUERIES(MEDIUM DEVICES) =========== */
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

/* ============ MEDIA QUERIES(SMALL DEVICES) =========== */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
  .header__socialmedia,
  .scroll__down {
    display: none;
  }
}



