footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: block;
}

.permalinks {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socialMedia {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socialMedia a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: -ms-flexbox;
    display: flex;
    border: 1px solid transparent;
}

.footer__socialMedia a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}

/* ============ MEDIA QUERIES(SMALL DEVICES) =========== */
@media screen and (max-width: 600px) {
    .permalinks {
        -ms-flex-direction: column;
            flex-direction: column;
        gap: 1.5rem;
    }

    .footer__socialMedia {
        margin-bottom: 2.6rem;
    }
}


